<template>

    <div class="wrap">
        <div class="left">
            <div class="search1">
                <el-input placeholder="搜索分类" prefix-icon="el-icon-search" v-model="search1"></el-input>
            </div>
            <div class="add">
                <el-button type="primary" icon="el-icon-plus" style="font-weight: 700">新增</el-button>
            </div>
            <div class="tree">
                <el-tree :data="treeData" :props="defaultProps" @node-click="handletreeNodeClick"
                    highlight-current="true" empty-text="树形控件数据为空" default-expanded-keys='[1,0]' style="color:#fa4e21;">
                </el-tree>
            </div>
        </div>
        <div class="right">
            <div class="rtop">
                <div class="rleft">
                    <div class="cont">{{ datafilter }}</div>
                    <div class="des">
                        <div class="des1">
                            类型：<span>商品</span>
                        </div>
                        <div class="des2">
                            创建时间：<span>2022-05-23</span>
                        </div>
                    </div>
                </div>
                <div class="rright">
                    <el-input placeholder="搜索分类" prefix-icon="el-icon-search" v-model="search1"
                        style="margin-right:20px;"></el-input>
                    <el-button type="primary" style="font-weight: 700" @click="dialogVisible = true">发布商品</el-button>
                </div>
            </div>
            <div class="rbottom">
                <el-table :data="tableData" style="width: 100%" highlight-current-row=true @row-click="clickTable">
                    <el-table-column type="selection" width="100">
                    </el-table-column>
                    <el-table-column width="140" align="center" label="商品名称">
                        <template slot-scope="scope">
                            <div class="tb">
                                <div class="tu">
                                    <img :src="require('@/assets/img' + scope.row.pic)" style="width:100%;height:100%;">
                                </div>
                                {{ scope.row.name }}
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="price" label="价格" align="center">
                    </el-table-column>
                    <el-table-column prop="inventory" label="库存" align="center">
                    </el-table-column>
                    <el-table-column prop="Sales" label="销量" align="center">
                    </el-table-column>
                    <el-table-column label="操作" align="center" width="200">
                        <template slot-scope="scope">
                            <div class="operate">
                                <el-button type="text" @click.stop="editproducts(scope.row)">编辑</el-button>
                                <el-button type="text" @click.stop="dialogVisible4 = true">删除</el-button>
                                <el-button type="text">下架</el-button>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="footer">
                    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                        :current-page.sync="currentPage1" :page-size="4" layout="total, prev, pager, next"
                        :total="this.tableData.length">
                    </el-pagination>
                </div>
            </div>
        </div>
        <!-- 发布商品 -->
        <el-dialog title="发布商品" :visible.sync="dialogVisible" width="50%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kung">
                <Listproducts />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 编辑商品 -->
        <el-dialog title="编辑商品" :visible.sync="dialogVisible2" width="50%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kung">
                <Editproducts :msg='propsmsg' />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible2 = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible2 = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 删除提示 -->
        <el-dialog title="提示" :visible.sync="dialogVisible4" width="25%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kuang">
                <Deletetag />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible4 = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible4 = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 商品详情 -->
        <el-dialog title="商品详情" :visible.sync="dialogVisible5" width="50%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kung">
                <ProductDetails :msg='propsmsg' @abc="getnum" @aaa="appraiseset" />
            </div>
        </el-dialog>
        <!-- 商品评价 -->
        <el-dialog title="商品评价" :visible.sync="dialogVisible6" width="50%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kung">
                <Appraise />
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { treeData, newShare } from '@/request/api'
import Listproducts from '../Newshare/components/listproducts'
import Editproducts from '../Newshare/components/Editproducts'
import Deletetag from '@/components/deletetag'
import ProductDetails from '../Newshare/components/ProductDetails1'
import Appraise from '../Newshare/components/appraise'

export default {
    beforeCreate() {
        treeData().then(res => {
            this.$data.treeData = res
        })
        newShare().then(res => {
            this.netData = res
            this.tableData = res.filter(e => e.cate == this.datafilter)
        })
    },
    data() {
        return {
            search1: null,
            dialogVisible: false,
            dialogVisible1: false,
            dialogVisible2: false,
            dialogVisible3: false,
            dialogVisible4: false,
            dialogVisible5: false,
            dialogVisible6: false,
            sels: [],
            propsmsg: null,
            treeData: null,
            netData: null,
            tableData: null,
            cate1: "女装",
            seld: "女装",
            datafilter: "连衣裙",
            defaultProps: {
                children: 'children',
                label: 'label'
            },
        }
    },
    watch: {
        datafilter(a, b) {
            this.tableData = this.netData.filter(e => e.cate == a)
        }
    },
    methods: {
        handletreeNodeClick(data) {
            if (data.children == undefined) {
                this.datafilter = data.label
            }
        },
        // // 关闭发布商品对话框
        // handleClose(done) {
        //     this.$confirm('确认关闭？')
        //         .then(_ => {
        //             done();
        //         })
        //         .catch(_ => { });
        // },
        // 分页
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
        },
        // 编辑对话框
        editproducts(val) {
            this.dialogVisible2 = true
            this.propsmsg = val
        },
        // 选择
        ss1() {
            if (this.sels.includes("1")) {
                this.sels.splice([this.sels.indexOf('1')], 1)
            } else {
                this.sels.push("1")
            }
        },
        ss2() {
            if (this.sels.includes("2")) {
                this.sels.splice([this.sels.indexOf('2')], 1)
            } else {
                this.sels.push("2")
            }
        },
        ss3() {
            if (this.sels.includes("3")) {
                this.sels.splice([this.sels.indexOf('3')], 1)
            } else {
                this.sels.push("3")
            }
        },
        ss4() {
            if (this.sels.includes("4")) {
                this.sels.splice([this.sels.indexOf('4')], 1)
            } else {
                this.sels.push("4")
            }
        },

        // 商品详情
        clickTable(row, index, e) {
            this.propsmsg = row
            this.dialogVisible5 = true
        },
        // 子组件通讯
        getnum(val1) {
            this.propsmsg = val1
            this.dialogVisible2 = true

        },
        appraiseset() {
            this.dialogVisible6 = true
        }
    },
    components: {
        Listproducts,
        Editproducts,
        Deletetag,
        ProductDetails,
        Appraise
    }
}
</script>
<style lang="scss" scoped>
* {
    box-sizing: border-box;
}

.wrap {
    width: 100%;
    height: 100%;
    padding: 20px;
    display: flex;

    .left {
        width: 25%;
        height: 100%;
        border-right: 1px solid #ddd;
        padding-right: 40px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .search1 {
            width: 100%;
        }

        .add {
            margin: 20px 0;
        }

        .tree {
            width: 100%;
        }
    }

    .right {
        width: 75%;
        height: 100%;
        padding-left: 30px;
        display: flex;
        flex-direction: column;

        .rtop {
            width: 100%;
            display: flex;
            justify-content: space-between;

            .rleft {
                display: flex;
                flex-direction: column;
                align-items: flex-start;

                .cont {
                    font-size: 18px;
                    font-weight: 700;
                }

                .des {
                    font-size: 14px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    color: #c5c5c5;
                    margin-top: 10px;
                    width: 260px;
                }
            }

            .rright {
                display: flex;
                align-items: center;
            }
        }

        .rbottom {
            margin-top: 20px;

            .tb {
                display: flex;
                width: 100%;
                height: 100%;
                align-items: center;
                justify-content: space-between;
                // background-color: red;

                .tu {
                    width: 55px;
                    height: 55px;
                    background-color: #c5c5c5;
                    border-radius: 10px;

                    img {
                        border-radius: 10px;
                    }
                }
            }
        }

        .footer {
            margin-top: 30px;
        }
    }

    .kung {
        width: 100%;
        height: 500px;
        overflow: auto;
        padding: 0;
    }

    .kuang {
        width: 100%;
        padding: 0;

        p {
            width: 100%;
            display: flex;
            justify-content: space-between;

            span {
                width: 120px;
            }
        }
    }

    .kuang1 {
        width: 100%;
        padding: 0;

        p {
            width: 100%;
            display: flex;

            span {
                color: #ddd;
            }
        }

        .sels {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            margin-top: 20px;

            .sel {
                width: 200px;
                height: 40px;
                background-color: #f8f8f8;
                border-radius: 10px;
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 0 20px 20px 0;

                .delsel {
                    position: absolute;
                    right: 0;
                    top: 0;
                }
            }

            .action {
                width: 200px;
                height: 40px;
                background-color: #fff2ef;
                color: #fa4e21;
                border-radius: 10px;
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 0 20px 20px 0;

                .delsel {
                    position: absolute;
                    right: 0;
                    top: 0;
                }
            }
        }
    }
}
</style>
